.app__gallery {
    background: var(--color-black);
    padding: 4rem 0 4rem 6rem;
}

.app__gallery-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: 500px;
    padding-right: 2rem;
}

.app__gallery-content button {
    margin-top: 1rem;
}

.app__gallery-images {
    flex: 1;
    display: flex;
    max-width: 50%;
    position: relative;
    margin-right: 5rem;
}

.app__gallery-images_container {
    display: flex;
    width: max-content;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.app__gallery-images_container::-webkit-scrollbar {
    display: none;
}

.app__gallery-images_card {
    position: relative;
    min-width: 301px;
    height: 447px;
    margin-right: 2rem;
}

.gallery__image-icon {
    position: absolute;
    color: #fff;
    font-size: 2rem;
    opacity: 0;
    transition: .5s ease;
    cursor: pointer;
}

.app__gallery-images_card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    transition: .5s ease;
}

.app__gallery-images_card:hover img {
    opacity: .35;
}
.app__gallery-images_card:hover .gallery__image-icon {
    opacity: 1;
}

.app__gallery-images_arrow {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    bottom: 5%;
}

.gallery__arrow-icon {
    color: var(--color-golden);
    font-size: 2rem;
    cursor: pointer;
    background: var(--color-black);
    border-radius: 5px;
    border: 3px solid var(--color-golden);
}
.gallery__arrow-icon:hover {
    color: var(--color-white);
    border: 3px solid var(--color-white);
}

@media screen and (min-width: 2000px) {
    .app__gallery-content {
        min-width: 1000px;
        padding-right: 4rem;
    }
    .app__gallery-images_card {
        min-width: 30%;
        height: 100%;
    }
    .gallery__arrow-icon{
        font-size: 4rem;
    }
}

@media screen and (max-width: 1150px) {
    .app__gallery {
        flex-direction: column;
    }
    .app__gallery-images {
        max-width: 100%;
        margin: 5rem 2rem 0 0;
    }
    .app__gallery-images_container {
        margin-right: 4rem;
    }
    .app__gallery-content {
        margin-right: 4rem;
    }
    .app__gallery-images_arrow {
        padding-right: 4rem;
    }
}

@media screen and (max-width: 650px) {
    .app__gallery {
        padding: 4rem 0 4rem 2rem;
    }
    .app__gallery-content {
        min-width: 100%;
        margin-left: 4rem;
    }
    .app__gallery-images_container {
        margin: 1rem;
    }
    .app__gallery-images_arrow {
        padding: 1rem;
    }
}   